.btn svg {
  background: #FFFFFF;
  border-radius: 2px;
}

.logo {
  width: 80px;
  height: auto;
  order: -1;
}

.sidebarHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  margin-top: 10px;
}

.sidebarDivider {
  background-color: white;
  width: 500px;
  margin-top: 20px;
}

.btnclose {
  padding: 0;
  margin: 0;
  background: transparent;
  border: none;
  cursor: pointer;
  margin-left: 50px;
  font-size: 40px;
}

.logo {
  width: 80px;
  height: auto;
  margin-right: 10px;
}

.logoText {
  display: flex;
  align-items: center;
}

.logoTextSynaps {
  color: white;
  font-weight: bold;
  font-size: 30px;

}

.logoTextSolutions {
  color: red;
  font-weight: bold;
  font-size: 30px;
  margin-left: 5px;
}

.Divider {
  color: #FFFFFF;
}

.listItem {
  padding: 10px;
  font-size: 20px !important;
}

.listItem:hover {
  color: #ff0000;
}

.listItemButton {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 20px !important;
}

.listItemIcon {
  min-width: 32px;
}

.span1 svg {
  margin-top: 35px;
}

@media (max-width:991px) {

  .bitech {
    position: absolute;
    top: 34px;
    left: 30px;
    font-size: 28px;
    font-weight: 500;
    text-decoration: none;

  }
}